import React, { Component } from 'react';
import '../Styles/Profile.css';
import RestaurantData from './RestaurantData';
import MD from '../static_resources/mcdonalds.jpg';
import W from '../static_resources/wendys.jpg';
import TB from '../static_resources/tacobell.jpg';


class RestaurantProfile extends Component {
  render() {
    const mcdonalds = {
      name: 'McDonald\'s',
      cuisine: 'American',
      location: '1209 Barton Springs Rd, Austin, TX 78704',
      hours_open: '5:00 am - 11:00 pm',
      phone_number: '(512) 442-0412',
      avg_rating: '3.3/5.0',
      price: '$',
      currency: 'USD',
      online_order: 'Yes',
      table_booking: 'No',
      image: MD,
    };
    const wendys = {
      name: 'Wendy\'s',
      cuisine: 'American',
      location: '201 E 21st St, Austin, TX 78705',
      hours_open: '6:30 am - 4:00 am',
      phone_number: '(512) 232-1929',
      avg_rating: '4.1/5.0',
      price: '$',
      currency: 'USD',
      online_order: 'Yes',
      table_booking: 'No',
      image: W,
    };
    const taco_bell = {
      name: 'Taco Bell',
      cuisine: 'Mexican',
      location: '2000 Guadalupe St, Austin, TX 78705',
      hours_open: '10:00 am - 3:00am',
      phone_number: '(737) 202-4309',
      avg_rating: '3.8/5.0',
      price: '$',
      currency: 'USD',
      online_order: 'Yes',
      table_booking: 'No',
      image: TB,
    };
    const restaurant_name = this.props.match.params.restaurantname;

    if (restaurant_name === 'mcdonalds') {
      return (
        <div>
          <RestaurantData rest={mcdonalds} />
        </div>
      );
    } else if (restaurant_name === 'wendys') {
      return (
        <div>
          <RestaurantData rest={wendys} />
        </div>
      );
    } else {
      return (
        <div>
          <RestaurantData rest={taco_bell} />
        </div>
      );
    }
  }
}

export default RestaurantProfile;
