import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import Universities from '.././Pages/Universities.js';
import UniversityProfile from '.././Pages/UniversityProfile.js';

class UniversityRouter extends Component {
  render() {
    return (
      <div>
        <Route exact path={this.props.match.path} component={Universities} />
        <Route
          path={`${this.props.match.path}/:universityname`}
          component={UniversityProfile}
        />
      </div>
    );
  }
}

export default UniversityRouter;
