import React, { Component } from 'react';
import '../Styles/Profile.css';
import CityData from './CityData';
import Austin from '../static_resources/austin_tx.png';
import SanFrancisco from '../static_resources/sanfrancisco_ca.png';
import Seattle from '../static_resources/seattle_wa.png';

class CityProfile extends Component {
  render() {
    const sanfrancisco_ca = {
      name: 'San Francisco, CA',
      age: '38.5',
      sunnyDays: '259',
      housePrice: '$1,119,000',
      crimeRate: '465.3',
      economicGrowth: '2.6%',
      eduLevel: 'B.S./Masters',
      politics: 'Very Liberal',
      diversity: 'Very Diverse',
      airQuality: '104',
      image: SanFrancisco,
    };
    const seattle_wa = {
      name: 'Seattle, WA',
      age: '35.2',
      sunnyDays: '152',
      housePrice: '$673,100',
      crimeRate: '449.6',
      economicGrowth: '4.2%',
      eduLevel: 'B.S./Masters',
      politics: 'Liberal',
      diversity: 'Somewhat Diverse',
      airQuality: '81.7',
      image: Seattle,
    };
    const austin_tx = {
      name: 'Austin, TX',
      age: '31.8',
      sunnyDays: '228',
      housePrice: '$332,700',
      crimeRate: '304.1',
      economicGrowth: '3.8%',
      eduLevel: 'B.S.',
      politics: 'Liberal',
      diversity: 'Diverse',
      airQuality: '75.7',
      image: Austin,
    };
    const city_name = this.props.match.params.cityname;

    if (city_name === 'sanfrancisco_ca') {
      return (
        <div>
          <CityData city={sanfrancisco_ca} />
        </div>
      );
    } else if (city_name === 'seattle_wa') {
      return (
        <div>
          <CityData city={seattle_wa} />
        </div>
      );
    } else {
      return (
        <div>
          <CityData city={austin_tx} />
        </div>
      );
    }
  }
}

export default CityProfile;
