import React, { Component } from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { Route, BrowserRouter } from 'react-router-dom';
import Home from './Pages/Home.js';
import About from './Pages/About.js';
import UniversityRouter from './Components/UniversityRouter.js';
import Api from './Pages/Api.js';
import CityRouter from './Components/CityRouter.js';
import RestaurantRouter from './Components/RestaurantRouter.js';

class App extends Component {
  render() {
    return (
      <div className="App">
        <BrowserRouter>
          <Route exact path="/" component={Home} />
          <Route path={'/universities'} component={UniversityRouter} />
          <Route path={'/about'} component={About} />
          <Route path={'/home'} component={Home} />
          <Route path={'/api'} component={Api} />
          <Route path={'/restaurants'} component={RestaurantRouter} />
          <Route path={'/cities'} component={CityRouter} />
        </BrowserRouter>
      </div>
    );
  }
}
export default App;
