import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import Restaurants from '.././Pages/Restaurants.js';
import RestaurantProfile from '.././Pages/RestaurantProfile.js';

class RestaurantRouter extends Component {
  render() {
    return (
      <div>
        <Route exact path={this.props.match.path} component={Restaurants} />
        <Route
          path={`${this.props.match.path}/:restaurantname`}
          component={RestaurantProfile}
        />
      </div>
    );
  }
}

export default RestaurantRouter;
